.profileInfo {
    display: flex;
    color: white;
}

.profilePhoto {
    display: flex;
    flex-direction: column;
}

.profilePhoto img {
    object-fit: cover;
    width: 200px;
    height: 300px;
    border-radius: 15px;
    margin-left: 10px;
    margin-right: 35px;
}

.profileInfo_userInfo {
    width: 55%;
}

.profileInfo_buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
}

.profileInfo_buttons>*:hover {
    background-color: white;
}

.visuallyHidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0)
}

.button__choosePhoto {
    box-sizing: border-box;
    margin-top: 5px;
    padding: 1px 6px;
    background-color: rgb(240, 240, 240);
    border: 2px solid rgb(118, 118, 118);
    border-bottom-color: rgb(0, 0, 0);
    border-right-color: rgb(0, 0, 0);
    border-radius: 4px;
    color: rgb(0, 0, 0);
    text-align: center;
    cursor: pointer;
}

.button__choosePhoto::before {
    content: '\f030';
    font-family: FontAwesome;
    margin-right: 5px;
}

.labelOutline {
    border-color: rgb(0, 0, 0);
    outline: 1px solid white;
}

.button__editProfile {
    margin-top: 5px;
    border-radius: 4px;
    font-size: 15px;
    cursor: pointer;
}

.button__editProfile::before {
    content: '\f044';
    font-family: FontAwesome;
    margin-right: 5px;
}